.list {
  justify-content: space-between;
  background-color: #fff;
  width: 320px;

  margin: 0 auto;

  margin-top: 20px;

  border-radius: 20px;
  padding: 10px;
}

.item {
  display: flex;

  background-color: rgba(12, 78, 16, 0.2);
  width: 300px;

  padding: 20px 0;
  margin: 10px;

  border-radius: 20px;

  align-items: center;
}

.name {
  font-size: 20px;
  font-weight: 900;
  line-height: 19px;

  margin-left: 20px;
}

.online {
  background-color: green;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  margin: 0 10px;
}
.ofline {
  background-color: red;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  margin: 0 10px;
}
